import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { HomeComponent } from './frontend/home/home.component';
import { HttpModule } from '@angular/http';
import {FormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { AuthGuard } from './providers/auth.guard';
import { PageNotFoundComponent } from './re-usable/page-not-found/page-not-found.component';
// {path:'admin/home',loadChildren:'./home/home.module#HomeModule'},
// loadChildren:'./admin/admin.module#AdminModule'
const routes: Routes = [
  { path: '', component: HomeComponent },
  {
    path: 'admin',loadChildren:'./admin/admin.module#AdminModule'
  },
  {
    path:'**',component:PageNotFoundComponent
  }

];

@NgModule({
  declarations:[HomeComponent],
  imports: [HttpClientModule,HttpModule,FormsModule,CommonModule,RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
