import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { ConfigJson } from './config.service'
@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private getAuth = "";
  constructor(public http: Http) {


  }

  getToken() {
    let token = "";
    if (localStorage.getItem('userData')) {
      let getUserData = JSON.parse(localStorage.getItem('userData'));
      if (getUserData['token']) {
        token = getUserData['token'];
      }
    }
    return token;
  }
  getMovies(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${ConfigJson.url}/movies`, data, { headers: headers }).pipe(map((response) => {
      return response.json();
    }), catchError(this.handleError));
  }
  updateMovie(data): Observable<any> {
    let token = this.getToken();
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + (token) });
    return this.http.post(`${ConfigJson.url}/movies/update`, data, { headers: headers }).pipe(map((response) => {
      return response.json();
    }), catchError(this.handleError));
  }
  createMovie(data): Observable<any> {
    let token = this.getToken();
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + (token) });
    return this.http.post(`${ConfigJson.url}/movies/create`, data, { headers: headers }).pipe(map((response) => {
      return response.json();
    }), catchError(this.handleError));
  }
  userLogin(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${ConfigJson.url}/user/login`, data, { headers: headers }).pipe(map((response) => {
      return response.json();
    }), catchError(this.handleError));
  }
  deleteMovie(data): Observable<any> {
    let token = this.getToken();
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + (token) });
    return this.http.post(`${ConfigJson.url}/movies/delete/${data.id}`, data, { headers: headers }).pipe(map((response) => {
      return response.json();
    }), catchError(this.handleError));
  }
  handleError(error) {
    return Observable.throw(error || 'Server error');
  }
}
